import React, {useState, useEffect} from 'react';
import {useAuthStateManager} from '../../hooks/useAuthStateManager';

interface StorageData {
    [key: string]: unknown;
}

interface DebugStorageState {
    localStorage: StorageData;
    sessionStorage: StorageData;
    cookies: StorageData;
}

const DebugPage: React.FC = () => {
    const authStateManager = useAuthStateManager();
    const [storageData, setStorageData] = useState<DebugStorageState>({
        localStorage: {},
        sessionStorage: {},
        cookies: {}
    });

    useEffect(() => {
        // Get localStorage data
        const localStorageData: StorageData = {};
        for (let i = 0; i < localStorage.length; i++) {
            const key = localStorage.key(i);
            if (key) {
                try {
                    localStorageData[key] = JSON.parse(
                        localStorage.getItem(key) || ''
                    );
                } catch (e) {
                    localStorageData[key] = localStorage.getItem(key);
                }
            }
        }

        // Get sessionStorage data
        const sessionStorageData: StorageData = {};
        for (let i = 0; i < sessionStorage.length; i++) {
            const key = sessionStorage.key(i);
            if (key) {
                try {
                    sessionStorageData[key] = JSON.parse(
                        sessionStorage.getItem(key) || ''
                    );
                } catch (e) {
                    sessionStorageData[key] = sessionStorage.getItem(key);
                }
            }
        }

        // Get cookies
        const cookiesData: StorageData = {};
        document.cookie.split(';').forEach(cookie => {
            if (cookie.trim()) {
                const [key, value] = cookie.trim().split('=');
                if (key) {
                    cookiesData[key] = value;
                }
            }
        });

        setStorageData({
            localStorage: localStorageData,
            sessionStorage: sessionStorageData,
            cookies: cookiesData
        });
    }, []);

    return (
        <div className="container mt-4">
            <h1 className="display-4">Debug Page</h1>

            <div className="card mb-4">
                <div className="card-header bg-primary text-white">
                    <h2 className="h5 mb-0">Auth State</h2>
                </div>
                <div className="card-body">
                    <pre className="mb-0">
                        {JSON.stringify(authStateManager.authData, null, 2)}
                    </pre>
                </div>
            </div>

            <div className="card mb-4">
                <div className="card-header bg-success text-white">
                    <h2 className="h5 mb-0">Local Storage</h2>
                </div>
                <div className="card-body">
                    <pre className="mb-0">
                        {JSON.stringify(storageData.localStorage, null, 2)}
                    </pre>
                </div>
            </div>

            <div className="card mb-4">
                <div className="card-header bg-info text-white">
                    <h2 className="h5 mb-0">Session Storage</h2>
                </div>
                <div className="card-body">
                    <pre className="mb-0">
                        {JSON.stringify(storageData.sessionStorage, null, 2)}
                    </pre>
                </div>
            </div>

            <div className="card mb-4">
                <div className="card-header bg-warning text-dark">
                    <h2 className="h5 mb-0">Cookies</h2>
                </div>
                <div className="card-body">
                    <pre className="mb-0">
                        {JSON.stringify(storageData.cookies, null, 2)}
                    </pre>
                </div>
            </div>
        </div>
    );
};

export default DebugPage;
